    font-family: 'Poppins', sans-serif;
}
.main-bg-con
{
    width: 100%;
    float:left;
    background: url(../../presale_assets/footer-bg.png) center bottom no-repeat;
}
*
{
    box-sizing: border-box;
}
.container-con
{
    width:1120px;
    margin:0 auto;
}
