/*timer*/
p#timer
{
    padding-left:25px;
}
p#timer div 
{
    float: left;
    width:72px;
    height: 72px;
    background: url(../../presale_assets/timer-bg.png) left top no-repeat;
    text-align: center;
    margin-right: 10px;
    font-size: 28px;
    color:#fff;
    font-weight: 700;
    font-family: 'Inter', sans-serif;
    padding-top:10px;
    position:relative;
}
p#timer div::after
{
    content:":";
    position: absolute;
    right:-9px;
    top:20px;
    color:#737f8b;
    font-size:22px;

} 
p#timer div:last-child::after 
{
    display:none;
}
p#timer div span 
{
    width:100%;
    float:left;
    font-size: 12px;
    text-transform: uppercase;
}
